___CSS_LOADER_EXPORT___.push([module.id, `* {
    margin: 0;
    padding: 0;
}

#header {
    background-color: #93f3f7;
    padding: 1rem;
    border-radius: 20px;
    margin: 10px;
}

#logo {
    width: 50px;
    height: 50px;
    display: block;
    aspect-ratio: 1;
    object-fit: contain;
}

.header-container {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 1rem;
    padding: 1rem;
    padding-left: 2rem;
    padding-right: 2rem;
    border-radius: 20px;
    width: fit-content;
    margin: auto;
    background-color: #a8c4ff;
}

#container {
    display: grid;
    grid-template-columns: 300px 1fr;
    margin: 5px;
    height: 80vh;
    max-width: 1300px;
    gap: 5rem;
    margin: 0 auto;
    padding: 1rem;
}

#sidebar {
    background-color: #a8ffac;
    padding: 1rem;
    border-radius: 20px;
    margin: 5px;
    display: grid;
    grid-template-rows: 50px 1fr;
    gap: 1rem;
    height: 70vh;
    width: 100%;
    position: sticky;
    left: 0;
}

#todo-list {
    overflow-y: auto;
    overflow-x: hidden;
    display: flex;
    flex-direction: column;
    gap: 10px;
    padding: 5px;
}

.todo-item {
    background-color: #a8c4ff;
    padding: 10px;
    border-radius: 20px;
    margin: 5px;
    border: 0.5px solid #8f8f8f;
    text-align: left;
    width: 80%;
    cursor: pointer;
    font-size: 1rem;
    overflow-wrap: break-word;
}

.todo-item h3 {
    margin: 0 0 0.5rem 0;
    color: #333;
}

.todo-item p {
    margin: 0.25rem 0;
    color: #666;
}

.priority {
    display: inline-block;
    background-color: #ff6b6b;
    color: white;
    padding: 0.25rem 0.5rem;
    border-radius: 4px;
    font-size: 0.8rem;
    margin-top: 0.5rem;
}

.todo-item:hover {
    background-color: #ebebeb;
}
.todo-item:active {
    background-color: #8f8f8f;
}

#content {
    background-color: #a8c4ff;
    padding: 1rem;
    border-radius: 20px;
    margin: 5px;
    height: 95%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
}

#todo-form {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    padding: 1rem;
    border-radius: 20px;
    background-color: #d2e470;
    width: 95%;
    height: 100%;
    margin: 1rem auto;
}

#todo-form-header {
    display: flex;
    flex-direction: row;
    gap: 1rem;
}
#form-description-container {
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
    margin: 1rem 0;
}

#form-description {
    min-height: 150px;
    padding: 0.5rem;
    border: 1px solid #ccc;
    border-radius: 4px;
    font-family: inherit;
    resize: none;
    font-size: 1rem;
}

#form-description:focus {
    outline: none;
    border-color: #93f3f7;
    box-shadow: 0 0 5px rgba(147, 243, 247, 0.5);
}

#add-todo {
    background-color: #93f3f7;
    border: none;
    border-radius: 8px;
    padding: 0.5rem;
    font-family: inherit;
    font-size: 1rem;
    cursor: pointer;
}

#submit-todo {
    background-color: #9d93f7;
    border: none;
    border-radius: 4px;
    padding: 1rem;
    font-family: inherit;
    font-size: 1rem;
    cursor: pointer;
    width: fit-content;
    margin-left: auto;
}

#todo-select {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    padding: 1rem;
    border-radius: 20px;
    background-color: #d2e470;
    width: 95%;
    height: 100%;
    margin: 1rem auto;
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA;IACI,SAAS;IACT,UAAU;AACd;;AAEA;IACI,yBAAyB;IACzB,aAAa;IACb,mBAAmB;IACnB,YAAY;AAChB;;AAEA;IACI,WAAW;IACX,YAAY;IACZ,cAAc;IACd,eAAe;IACf,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,uBAAuB;IACvB,SAAS;IACT,aAAa;IACb,kBAAkB;IAClB,mBAAmB;IACnB,mBAAmB;IACnB,kBAAkB;IAClB,YAAY;IACZ,yBAAyB;AAC7B;;AAEA;IACI,aAAa;IACb,gCAAgC;IAChC,WAAW;IACX,YAAY;IACZ,iBAAiB;IACjB,SAAS;IACT,cAAc;IACd,aAAa;AACjB;;AAEA;IACI,yBAAyB;IACzB,aAAa;IACb,mBAAmB;IACnB,WAAW;IACX,aAAa;IACb,4BAA4B;IAC5B,SAAS;IACT,YAAY;IACZ,WAAW;IACX,gBAAgB;IAChB,OAAO;AACX;;AAEA;IACI,gBAAgB;IAChB,kBAAkB;IAClB,aAAa;IACb,sBAAsB;IACtB,SAAS;IACT,YAAY;AAChB;;AAEA;IACI,yBAAyB;IACzB,aAAa;IACb,mBAAmB;IACnB,WAAW;IACX,2BAA2B;IAC3B,gBAAgB;IAChB,UAAU;IACV,eAAe;IACf,eAAe;IACf,yBAAyB;AAC7B;;AAEA;IACI,oBAAoB;IACpB,WAAW;AACf;;AAEA;IACI,iBAAiB;IACjB,WAAW;AACf;;AAEA;IACI,qBAAqB;IACrB,yBAAyB;IACzB,YAAY;IACZ,uBAAuB;IACvB,kBAAkB;IAClB,iBAAiB;IACjB,kBAAkB;AACtB;;AAEA;IACI,yBAAyB;AAC7B;AACA;IACI,yBAAyB;AAC7B;;AAEA;IACI,yBAAyB;IACzB,aAAa;IACb,mBAAmB;IACnB,WAAW;IACX,WAAW;IACX,aAAa;IACb,sBAAsB;IACtB,2BAA2B;AAC/B;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,SAAS;IACT,aAAa;IACb,mBAAmB;IACnB,yBAAyB;IACzB,UAAU;IACV,YAAY;IACZ,iBAAiB;AACrB;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,SAAS;AACb;AACA;IACI,aAAa;IACb,sBAAsB;IACtB,WAAW;IACX,cAAc;AAClB;;AAEA;IACI,iBAAiB;IACjB,eAAe;IACf,sBAAsB;IACtB,kBAAkB;IAClB,oBAAoB;IACpB,YAAY;IACZ,eAAe;AACnB;;AAEA;IACI,aAAa;IACb,qBAAqB;IACrB,4CAA4C;AAChD;;AAEA;IACI,yBAAyB;IACzB,YAAY;IACZ,kBAAkB;IAClB,eAAe;IACf,oBAAoB;IACpB,eAAe;IACf,eAAe;AACnB;;AAEA;IACI,yBAAyB;IACzB,YAAY;IACZ,kBAAkB;IAClB,aAAa;IACb,oBAAoB;IACpB,eAAe;IACf,eAAe;IACf,kBAAkB;IAClB,iBAAiB;AACrB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,SAAS;IACT,aAAa;IACb,mBAAmB;IACnB,yBAAyB;IACzB,UAAU;IACV,YAAY;IACZ,iBAAiB;AACrB","sourcesContent":["* {\r\n    margin: 0;\r\n    padding: 0;\r\n}\r\n\r\n#header {\r\n    background-color: #93f3f7;\r\n    padding: 1rem;\r\n    border-radius: 20px;\r\n    margin: 10px;\r\n}\r\